  .logo-pink-header {
    margin-left: 0;
  }
  .logo-pink-header__glasses {
    display: none;
  }
  .logo-pink-header__glasses-crop {
    display: block;
  }
  .logo-pink-header__stars,
  .logo-pink-header__star {
    display: block;
    fill: currentColor;
  }
}
