.main-nav__toggle {
  position: absolute;
  top: 20px;
  right: 16px;
  width: 50px;
  height: 25px;
  padding: 0;
  background-color: transparent;
  border: none;
  cursor: pointer;
  outline: none;
}
.main-nav--closed .main-nav__toggle span {
  display: block;
  width: 50px;
  height: 4px;
  background-color: #ffffff;
  border-radius: 1px;
}
.main-nav--closed .main-nav__toggle span::before,
.main-nav--closed .main-nav__toggle span::after {
  content: "";
  position: absolute;
  left: 0;
  width: 50px;
  height: 4px;
  background-color: #ffffff;
  border-radius: 1px;
}
.main-nav--closed .main-nav__toggle span::before {
  top: 0;
}
.main-nav--closed .main-nav__toggle span::after {
  bottom: 0;
}
.main-nav--opened .main-nav__toggle span::before,
.main-nav--opened .main-nav__toggle span::after {
  content: "";
  position: absolute;
  right: 0;
  top: 10px;
  width: 30px;
  height: 4px;
  background-color: #ffffff;
  border-radius: 1px;
}
.main-nav--opened .main-nav__toggle span::before {
  transform: rotate(45deg);
}
.main-nav--opened .main-nav__toggle span::after {
  transform: rotate(-45deg);
}
.main-nav__toggle:hover span,
.main-nav__toggle:hover span::before,
.main-nav__toggle:hover span::after {
  background-color: #d22856;
}
.main-nav__toggle:active span,
.main-nav__toggle:active span::before,
.main-nav__toggle:active span::after {
  background: rgba(210, 40, 86, 0.3);
}
.main-nav--opened .main-nav__list {
  margin-top: 4px;
}
@media (max-width: 959px) {
  .main-nav--closed .main-nav__list {
    display: none;
  }
  .main-nav--nojs .main-nav__toggle {
    display: none;
  }
  .main-nav--nojs .main-nav__list {
    display: block;
  }
}
@media (min-width: 660px) {
  .main-nav__toggle {
    top: 44px;
  }
  .main-nav--opened .main-nav__toggle span::before,
  .main-nav--opened .main-nav__toggle span::after {
    right: 0;
  }
  .main-nav--opened .main-nav__list {
    margin-top: 0;
  }
}
@media (min-width: 960px) {
  .main-nav {
    width: 760px;
    margin-left: auto;
    padding-top: 28px;
    padding-bottom: 28px;
    flex-shrink: 1;
  }
  .main-nav__toggle {
    display: none;
  }
}
.site-list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: block;
  text-align: center;
}
.site-list__item a {
  display: block;
  margin: 0;
  background-color: #283645;
  color: #ffffff;
  text-decoration: none;
  text-transform: uppercase;
  font-size: 18px;
  font-weight: 700;
  line-height: 32px;
  padding: 16px 0;
  box-shadow: 0 -1px 0 #000000, inset 0 1px 0 rgba(255, 255, 255, 0.15);
}
.site-list__item a:hover,
.site-list__item a:focus {
  color: #d22856;
}
.site-list__item a:active {
  color: rgba(210, 40, 86, 0.3);
}
.site-list__item--current a:hover,
.site-list__item--current a:focus {
  color: #ffffff;
}
.site-list__item:last-child a {
  box-shadow: 0 -1px 0 #000000, inset 0 1px 0 rgba(255, 255, 255, 0.15), inset 0 -1px 0 #000000;
}
@media (min-width: 960px) {
  .site-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
  }
  .site-list__item {
    position: relative;
    margin-left: 80px;
  }
  .site-list__item a {
    background-color: transparent;
    text-transform: none;
    box-shadow: none;
    font-weight: 400;
    padding: 10px 0 5px;
  }
  .site-list__item:last-child a {
    box-shadow: none;
  }
  .site-list__item--current {
    position: relative;
  }
  .site-list__item--current:after {
    content: "";
    position: absolute;
    right: 0;
    bottom: -2px;
    left: 0;
    height: 2px;
    background: #ffffff;
  }
}
