  fill: #d22856;
  position: absolute;
}
.social__icon--twitter {
  top: 15px;
  left: 14px;
  width: 18px;
  height: 15px;
}
.social__icon--facebook {
  top: 12px;
  left: 18px;
  width: 9px;
  height: 19px;
}
.social__icon--youtube {
  top: 15px;
  left: 17px;
  width: 15px;
  height: 14px;
}
@media (min-width: 660px) {
  .social {
    display: flex;
    margin-top: 0;
  }
  .social__link {
    margin-right: 11px;
    margin-left: 0;
  }
  .social__link:last-child {
