.intro {
  background-color: @blue-dark;
  background-repeat: no-repeat;
  background-position: center top;
  color: @white;
  padding-top: 70px;
  background-image: url("../img/bg-back-mobile@1x.jpg");

  @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
    background-image: url("../img/bg-back-mobile@2x.jpg");
    background-size: 320px auto;
  }

  @media (min-width: 321px) {
    background-image: url("../img/bg-back-tablet@1x.jpg");
  }

  @media (min-width: 321px) and (min-resolution: @retina-dpi), (min-width: 321px) and (min-resolution: @retina-dppx) {
    background-image: url("../img/bg-back-tablet@2x.jpg");
    background-size: 1200px auto;
  }

  @media (min-width: @tablets-width) {
    padding-top: 110px;
  }

  @media (min-width: @desktops-width) {
    background-image: url("../img/bg-back-desktop@1x.jpg");
  }

  @media (min-width: @desktops-width) and (min-resolution: @retina-dpi), (min-width: @desktops-width) and (min-resolution: @retina-dppx) {
    background-image: url("../img/bg-back-desktop@2x.jpg");
    background-size: 2560px auto;
  }
}

.intro__wrapper {
  .container;
  position: relative;

  &--inner {
    padding-top: 28px;
    padding-bottom: 28px;
    min-height: 169px;
  }

  &--hand::before {
    content: "";
    display: block;
    position: relative;
    width: 320px;
    height: 396px;
    bottom: 0;
    background-repeat: no-repeat;
    background-position: left bottom;
    margin-left: auto;
    margin-right: auto;
    background-image: url("../img/iphone-hand-mobile@1x.png");

    @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
      background-image: url("../img/iphone-hand-mobile@2x.png");
      background-size: 258px auto;
    }

    @media (min-width: @tablets-width) {
      background-image: url("../img/iphone-hand-tablet@1x.png");
    }

    @media (min-width: @tablets-width) and (min-resolution: @retina-dpi), (min-width: @tablets-width) and (min-resolution: @retina-dppx) {
      background-image: url("../img/iphone-hand-tablet@2x.png");
      background-size: 700px auto;
    }

    @media (min-width: @desktops-width) {
      background-image: url("../img/iphone-hand-desktop@1x.png");
    }

    @media (min-width: @desktops-width) and (min-resolution: @retina-dpi), (min-width: @desktops-width) and (min-resolution: @retina-dppx) {
      background-image: url("../img/iphone-hand-desktop@2x.png");
      background-size: 1050px auto;
    }
  }
}

.intro__title {
  font-size: 24px;
  line-height: 30px;
  font-weight: 300;
  margin: 0;
  text-align: center;

  &--hidden {
    .visually-hidden;
  }
}

@media (max-width: @phones-max-width) {
  .intro__wrapper {
    &--index {
      width: auto;
      padding: 0;
    }
  }
}

@media (max-width: @tablets-max-width) {
  .intro {
    &--short {
      padding-top: 0;
      background-position: center -69px;
    }
  }
}


@media (min-width: @tablets-width) {
  .intro__wrapper {
    height: 100%;

    &--index {
      min-height: 619px;
    }

    &--inner {
      padding-top: 69px;
      padding-bottom: 69px;
      min-height: 330px;
    }

    &--hand::before {
      position: absolute;
      bottom: 0;
      right: 0;
      width: 100%;
      height: 545px;
      background-position: -20px bottom;
      z-index: 1;
    }
  }

  .intro__title {
    font-size: 50px;
    line-height: 60px;
  }
}

@media (min-width: @desktops-width) {
  .intro {
    padding-top: 110px;
  }

  .intro__wrapper {
    &--index {
      min-height: 743px;
    }

    &--inner {
      min-height: 284px;
      padding-top: 63px;
      padding-bottom: 64px;
    }

    &--gallery {
      padding-left: 200px;
      padding-right: 200px;
    }

    &--contest {
      padding-left: 90px;
      padding-right: 90px;
    }

    &--hand::before {
      width: 100%;
      height: 645px;
      background-position: 26px bottom;
    }
  }
}
